@import (less) "color.less";

.paging-container{
    border-bottom: 2px solid @lightest_gray;
    margin-left: -25px;
    margin-right: -25px;
}

.sidebar-button__structure {
  font-size: 50px;
  color: @dark_cyan;
  text-decoration: none !important;
  cursor: pointer;
}

.sidebar-button__layout {
  position: fixed;
  top: 60px;
  z-index: 99999;
  right: 100px;
}

.transfrom-slim {
  top: 50px !important;
  font-size: 35px !important;
}

.sidebar__layout {
  position: fixed;
  right: 100px;
}


#wizard-content{
  padding-top: 3.125em;
  
  #component-container{
    min-height: 55vh;  
  }
}

::ng-deep .shopping-cart--content {
  max-height:50vh !important;
  overflow: hidden !important;
}

